import React, {useEffect, useState} from 'react';
import Footer from './Footer';
import Header from './Header';
import { useHistory } from 'react-router-dom';
import { Carousel } from 'react-responsive-carousel';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import '../../style/Home.css';
import { useStateValue } from '../../context/StateProvider';
import axios from 'axios';
import { addToCart, orderFoodItem, removeFromCart } from '../../context/util';

function Home() {
    const history = useHistory();
    const [{ account, isAuthenticated, loading, details }, dispatch] = useStateValue();
    const [data,setData] = useState([]);
    const [dataLoading,setDataLoading] = useState(true);

    useEffect(() => {
        const getDataFromApi = () => {
            axios
            .get('/api/restaurants/restaurantlist')
            .then(res => {
                setData((data) => res.data);
                setDataLoading(false);
            })
            .catch(err => console.log(err));
        }
        getDataFromApi();
    }, [])

    const AddCartClickHandler = (e, restaurantId, itemId) => {
        if(!isAuthenticated) {
            history.push('/login');
        } else {
            const foodData = {
                restaurantId: restaurantId,
                itemId: itemId
            }
            console.log(foodData);
            addToCart(foodData, dispatch);
        }
    }

    const RemoveCartClickHandler = (e, restaurantId, itemId) => {
        if(!isAuthenticated) {
            history.push('/login');
        } else {
            const foodData = {
                restaurantId: restaurantId,
                itemId: itemId
            }
            console.log(foodData);
            removeFromCart(foodData, dispatch);
        }
    }

    const onOrderClickHandler = (e, restaurantID, itemID) => {
        if(!isAuthenticated) {
            history.push('/login');
        } else {
            const foodData = {
                restaurantid: restaurantID,
                itemid: itemID
            }
            orderFoodItem(foodData, dispatch);
        }
    }

    return (
        <div>
            <Header/>
            <div className="primary-content-area">
                <div className="primary-content-wrap">
                    <section className="thunk-slider-section">
                        <div id="thunk-single-slider" className="ads-section">
                            <Carousel className="Carousel"
                                emulateTouch={true} 
                                showArrows={true} 
                                showStatus={false} 
                                infiniteLoop={true}
                                showThumbs={false} 
                                swipeable={true} 
                                autoPlay={true}
                                stopOnHover={true}
                                useKeyboardArrows={true}
                                transitionTime={700}
                                dynamicHeight={false}
                            >
                                <a href="/">
                                    <img alt="" src="data:image/jpeg;base64," />
                                </a>
                                <a href="/">
                                    <img alt="" src="data:image/jpeg;base64," />
                                </a>
                            </Carousel>
                        </div>
                    </section>
                    <section>
                        {
                            (loading || dataLoading) && <div>Loading...</div>
                        }
                        {
                            !dataLoading &&
                            data.map(restaurant => (
                                restaurant.items.length !==0 &&
                                <div>
                                    <h4>From {restaurant.name}</h4>
                                    <ul className="food-content">
                                        {
                                            restaurant.items &&
                                            restaurant.items.map(item => 
                                                <li className="listli">
                                                    <div className="food-item">
                                                        <div className="item-img">
                                                            <img
                                                                loading="lazy"
                                                                alt="food-item"
                                                                className="wooble"
                                                                src={item.img}
                                                            />
                                                            {
                                                                !isAuthenticated &&
                                                                <div className="add-to-cart">
                                                                    <button title="Add to cart" onClick={e => AddCartClickHandler(e, restaurant._id, item._id)} className="waves-effect waves-light hoverable orange cart-button">
                                                                        <i style={{color: 'white', fontSize: '16px'}} className="fa fa-cart-arrow-down"></i>
                                                                    </button>
                                                                </div>
                                                            }
                                                            {
                                                                (isAuthenticated && account.type === 'user') &&
                                                                <div className="add-to-cart">
                                                                    {
                                                                        !details.cart
                                                                        ?(<button title="Loading" className="waves-effect waves-light hoverable orange cart-button">
                                                                            <i style={{color: 'white', fontSize: '16px'}} className="fa fa-spin fa-spinner"></i>
                                                                        </button>)
                                                                        :(details.cart && !details.cart.find(cartItem => cartItem.itemId == item._id))
                                                                        ?(<button onClick={e => AddCartClickHandler(e, restaurant._id, item._id)} title="Add to cart" className="waves-effect waves-light hoverable orange cart-button">
                                                                            <i style={{color: 'white', fontSize: '16px'}} className="fa fa-cart-arrow-down"></i>
                                                                        </button>)
                                                                        :(<button onClick={e => RemoveCartClickHandler(e, restaurant._id, item._id)} title="Remove from cart" className="waves-effect waves-light hoverable orange cart-button">
                                                                            <i style={{color: 'white', fontSize: '16px'}} className="fa fa-times"></i>
                                                                        </button>)
                                                                    }
                                                                </div>
                                                            }
                                                        </div>
                                                        <div className="stats-container">
                                                            <div className="item-name">{item.name}</div>
                                                            <div className="item-cost-wrapper">
                                                                <span className="item-cost">₹{item.cost}</span>
                                                            </div>
                                                            <div className="item-type">({item.type})</div>
                                                            {
                                                                (!isAuthenticated || (isAuthenticated && account.type === 'user')) &&
                                                                <div className="item-order center" style={{marginTop: '6px'}}>
                                                                    <button onClick={e => onOrderClickHandler(e, restaurant._id, item._id)} className="btn waves-effect waves-light hoverable orange accent-3">
                                                                        Order now
                                                                    </button>
                                                                </div>
                                                            }
                                                        </div>
                                                    </div>
                                                </li>    
                                            )}
                                    </ul>
                                </div> 
                            ))}
                    </section>
                </div>
            </div>
            
            <Footer/>
        </div>
    )
}

export default Home
